<template>
<div>
   <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>Comisson</h1>
       <div class="chartWrap">
        <a href="javascript:void(0)" @click="show=true" class="iconfont cart"> &#xe669; </a></div>
    </section>
    <div class="center">
        <ul>
            <li>    
                <div class="register">
                    <div class="coat"><span class="title">Order Register</span><span class="value">2018-12-25 23:50:59</span></div>
                    <div class="userAed"><span class="title">user：<font>Alice</font></span><span class="value">+89 AED</span></div>
                </div>
            </li>
            <li>    
                <div class="generated">
                    <div class="coat"><span class="title">Order Register</span><span class="value">2018-12-25 23:50:59</span></div>
                    <div class="content">
                        <div class="name_left">
                            <span>Order USer: <font>Alice</font></span>
                            <span>Order Sum: <font>9999.99AED</font></span>
                        </div>
                        <div class="aed_right">
                            +68AED
                        </div>
                    </div>
                </div>
                <div class="cancelled">
                    <div class="coat"><span class="title">Order Register</span><span class="value">2018-12-25 23:50:59</span></div>
                    <div class="content">
                        <div class="name_left">
                            <span>Order USer: <font>Alice</font></span>
                            <span>Order Sum: <font>9999.99AED</font></span>
                        </div>
                        <div class="aed_right">
                            +68AED
                        </div>
                    </div>
                </div>
            </li>
        </ul>
        <div class="withdraw">
            <div class="coat"><span class="title">Cash Withdraw</span><span class="value">2018-12-25 23:50:59</span></div>
            <div class="aedWrap">-68 AED</div>
        </div>
        <van-popup v-model="show" position="rigth" style="height: 100%; width:80%;">
            <div class="selectTimeWrap">
                <div class="selectTime">
                    <div class="blocks">
                        <p>Select Time</p>
                        <div class="tags row">
                            <el-date-picker
                                v-model="value1"
                                placeholder="Start time"                 
                                type="date">
                            </el-date-picker>
                            <div class="colM">—</div>
                            <el-date-picker
                                v-model="value1"
                                placeholder="End time"    
                                type="date">
                            </el-date-picker>
                        </div>
                    </div> 
                    <div class="radioBlocks">
                        <p> <span>Commission Types</span> <span class="iconfont red">&#xe671;</span></p>
                        <div>
                            <ul>
                                <li class="red">
                                    <label>User Orders</label>
                                    <span class="iconfont"> &#xe608;</span>
                                </li>
                                <li>
                                    <label>User Registered</label>
                                    <span class="iconfont"> &#xe608;</span>
                                </li>
                                <li>
                                    <label>Cash Withdraw</label>
                                    <span class="iconfont"> &#xe608;</span>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div class="downBtnWrap">
                        <van-button @click="show=false"  type="danger">Reset</van-button>
                        <van-button @click="show=false"  type="danger">Confirm</van-button>
                    </div>
                </div>
            </div>
            </van-popup>   
    </div>
     <van-popup v-model="show2" position="bottom">
          
    </van-popup>
</div>
</template>

<script>
import {commission} from '@/api/store'
import {mapGetters} from 'vuex'
export default {
  name: 'comisson',
  data () {
    return {
        commissionList:{},
        show: false,
        show2: false,
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        value1: '',
    }
  },
  computed: {
       ...mapGetters(['indexData'])
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){
        commission({pageNo:1,pageSize:10}).then(res=>{
            this.commissionList=res.data.result;
        })
    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    }


  }
}
</script>
<style lang="scss">
    .el-picker-panel{
        z-index: 2600 !important;
        width: 100%;
        display: block !important;
        bottom: 0;
        top: 46% !important;
    }
    .row{
        .el-input__inner{
            padding: 0;
            -webkit-box-flex: 0;
            -ms-flex: 0 0 42%;
            flex: 0 0 42%;
            width: 111.3px;
            height: 28px;
            border: #666666 1px solid;
            border-radius: 2px;
            text-align: center;
            
        }
        .el-input__prefix{
            display: none;
        }
    }
    .el-input{
        width: 111.3px !important;
    }
    .blocks{
        input::-webkit-input-placeholder{
            color: #666666;
        }
    }
    .el-input__suffix-inner{
        display: none;
    }
    .el-date-picker .el-picker-panel__content{
        width: 92%;
    }
</style>
<style lang="scss" scoped>
    .center{
        .van-popup{
            left: 60%;
        }
        .withdraw{
            border: #f6f6f6 1px solid;
            padding: 0 4%;
             .coat{
                width: 100%;
                padding: 10px 0 0 0;
                display: -webkit-box;
                span{
                    width: 50%;
                    display: block;
                    color: #999999;
                }
                .value{
                    text-align: right;
                }
                .title{
                    font-size: 15px;
                    color: #333333;
                    font-weight: 500;
                }
            }
            .aedWrap{
                text-align: right;
                font-size: 15px;
                padding-bottom: 10px;
                color: #333333;
            }
        }
    }
    ul{
        width: 100%;
        border-top: 10px solid #f6f6f6;
        border-bottom: 10px solid #f6f6f6;
    }
    li{
        width: 100%;
        padding-left: 4%;
        .register,.generated,.cancelled{
            border: #f6f6f6 1px solid;
            padding: 0 4% 0 0;
            .coat{
                width: 100%;
                padding: 10px 0 0 0;
                display: -webkit-box;
                span{
                    width: 50%;
                    display: block;
                    color: #999999;
                }
                .value{
                    text-align: right;
                }
                .title{
                    font-size: 15px;
                    color: #333333;
                    font-weight: 500;
                }
            }
            .userAed{
                width: 100%;
                padding: 3px 0 10px 0;
                display: -webkit-box;
                span{
                    width: 50%;
                    display: block;
                    color: #b7b7b7;
                }
                .value{
                    text-align: right;
                    color: #E33946;
                    font-size: 15px;
                }
                .title{
                    font-size: 13px;
                    color: #666666;
                    font{
                        color: #333333;
                        font-size: 14px;
                    }
                }
            }
            .content{
                width: 100%;
                display: -webkit-box;
                padding: 5px 0 10px 0;
                .name_left{
                    width: 50%;
                    span{
                        display: block;
                        color: #666666;
                        padding-top: 5px;
                        font-size: 13px;
                        font{
                            color: #333333;
                            font-size: 14px;
                        }
                    }
                }
                .aed_right{
                    width: 50%;
                    display: flex; align-items: center; justify-content: flex-end;
                    color: #E33946;
                    font-size: 15px;
                }
            }
        }
    }
    .cancelled .content .aed_right{
        color: #333333 !important;
    }
    .selectTimeWrap{
        padding-bottom: 55px;
        overflow-y: scroll;
        padding-top: 20px;
        height: 100%;
        position: relative;
        .selectTime{
            font-size: 13px;
            background: #f4f4f4;
            .blocks{
                padding: 10px 15px 10px 20px;
                background-color: #fff;
                p{
                    color: #333333;
                    margin: 15px 0 10px;
                    font-size: 15px;
                    display: -webkit-box;
                    display: -ms-flexbox;
                    display: flex;
                    -webkit-box-pack: justify;
                    -ms-flex-pack: justify;
                    justify-content: space-between;
                }
                .tags{
                    a{display:block; color:#666666; border-radius: 2px; border:solid 1px #666666; line-height: 26px; width:100%; text-align: center; }
                    a.active{color:#e33946; border-color:#e33946;}
                }
                .row{display: flex; align-items: center;
                    .col{flex: 0 0 42%;}
                    .colM{flex: 0 0 8%; text-align: center;}
                }   
            }
            .radioBlocks{
                padding: 10px 0;
                background-color: #fff;
                margin-top: 10px !important;
                p{
                    color: #333333;
                    margin-top: 5px;
                    font-size: 15px;
                    display: -webkit-box;
                    display: -ms-flexbox;
                    display: flex;
                    -webkit-box-pack: justify;
                    -ms-flex-pack: justify;
                    justify-content: space-between;
                    padding: 0 15px 15px 20px;
                    border-bottom: solid 1px #e5e5e5;
                }
                ul{
                    padding: 0 15px 0 20px;
                    width: 100%;
                    border-top: none;
                    border-bottom: none;
                    li{
                        padding: 15px 0;
                        border-bottom: solid 1px #e5e5e5;
                        display: -webkit-box;
                        display: -ms-flexbox;
                        display: flex;
                        -webkit-box-pack: justify;
                        -ms-flex-pack: justify;
                        justify-content: space-between;
                    }
                }
            }
            .downBtnWrap{position:fixed; left: 0; right:0 ; bottom:0; background: #fff; padding: 5px 25px 20px; display: flex; justify-content: space-between;
                .van-button{width:45%; height: 32px; line-height: 30px;}
                .van-button:first-child{background-color: #fff; color:#e33946;}
            }
        }
    }
</style>





